.app {
  overflow-x: hidden;
  position: absolute;
  background-color: #FFF;
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  overflow: hidden;
}

.img {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/*
Header things
 */

.container_up_space {
  height: 130px;
}

.header_up_space {
  height: 20px;
}

.menu_item__container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.menu_item {
