/*
Instructions
 */

.stepper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.steps__array {
  margin-top: 64px;
}

.step__button {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.step__button_text {
