@media (max-width: 1650px) {
  .header__short_form {
    width: 250px;
  }
}

@media (max-width: 1450px) {
  .header {
    flex-wrap: wrap;
  }

  .header__short_form {
    width: 0px;
    visibility: hidden;
    height: 0px;
  }

  .header_menu {
    width: 60%;
  }
}

@media (max-width: 1300px) {
  .footer {
    flex-direction: column;
  }

  .footer_column {
    margin-left: 64px;
    margin-top: 15px;
  }

  .footer_logo {
    top: initial;
  }

  .rate_form {
    width: 50%;
  }

  .footer__short_form {
    width: 100%;
